<template>
  <div id="app">
    <v-app id="inspire">
      <div>
        <v-data-table
          :headers="headers"
          :items="desserts"
          :page.sync="page"
          :items-per-page="itemsPerPage"
          hide-default-footer
          class="elevation-1"
          @page-count="pageCount = $event"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title></v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <v-col cols="6" class="py-2">
                <v-row align="end">
                  <v-btn value="left" @click="getItem('all')"> </v-btn>
                  <v-menu offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn v-bind="attrs" v-on="on"></v-btn>
                    </template>
                    <v-list>
                      <v-list-item v-for="(item, index) in items" :key="index">
                        <v-list-item-title @click="getStatus(item.index)">{{
                          item.title
                        }}</v-list-item-title>
                      </v-list-item>
                    </v-list>
                  </v-menu>
                  <v-btn value="left" @click="getItem('left')"> </v-btn>
                  <v-btn value="center" @click="getItem('center')"
                    ></v-btn
                  >
                  <v-btn value="right" @click="getItem('right')">
                    
                  </v-btn>
                </v-row>
              </v-col>
              <v-spacer></v-spacer>
              <v-dialog max-width="500px" v-model="dialogDelete">
                <v-card>
                  <v-card-title class="text-h5"></v-card-title>
                  <v-card-actions>
                    <v-btn color="error darken-1" @click="auitYes"></v-btn>
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogUpdate" max-width="500px">
                <v-card>
                  <v-card-title>
                    <span class="text-h5"></span>
                  </v-card-title>
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            v-model="user.username"
                            label=""
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            v-model="user.schoolName"
                            label=""
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            v-model="user.profession"
                            label=""
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            v-model="user.phone"
                            label=""
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field
                            v-model="user.email"
                            label=""
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="closeDialog">
                      
                    </v-btn>
                    <v-btn color="blue darken-1" text @click="update">
                      
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:[`item.img`]="{ item }">
            <img :src="item.img" style="width: 50px; height: 50px" />
          </template>
          <template v-slot:[`item.status`]="{ item }">
            <v-btn v-if="item.status === 0" color="blue-grey"> </v-btn>
            <v-btn v-else-if="item.status === 1" color="primary"
              ></v-btn
            >
            <v-btn v-else color="error"></v-btn>
          </template>
          <template v-slot:[`item.actions`]="{ item }" v-if="show">
            <v-icon small @click="updateProduct(item)">  </v-icon>
            <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
          </template>
          <template v-slot:no-data>
            <v-btn color="primary" @click="initialize">  </v-btn>
          </template>
        </v-data-table>
        <div class="text-center pt-2">
          <v-pagination
            v-model="page"
            :length="count"
            @input="initialize"
          ></v-pagination>
        </div>
      </div>
    </v-app>
    <v-btn class="mx-2" fab dark large color="purple" id="bottom" @click="home">
      
    </v-btn>
  </div>
</template>
<script>
import Product from "../../services/product";
export default {
  data() {
    return {
      page: 1,
      pageCount: 0,
      itemsPerPage: 8,
      dialogDelete: false,
      dialogUpdate: false,
      editedIndex: "",
      condition: 0,
      text: "center",
      show: true,
      userId: this.$store.state.auth.user,
      status: null,
      count: 0,
      user: {
        username: "",
        schoolName: "",
        profession: "",
        role: "admin",
        phone: "",
        email: "",
        password: "",
      },
      headers: [
        {
          text: "",
          align: "start",
          sortable: false,
          value: "title",
        },
        { text: "", sortable: false, value: "price" },
        { text: "", sortable: false, value: "img" },
        { text: "", sortable: false, value: "desc" },
        { text: "", sortable: false, value: "categories" },
        { text: "", sortable: false, value: "status" },
        { text: "", sortable: false, value: "updatedAt" },
      ],
      desserts: [],
      items: [
        { title: "", index: 0 },
        { title: "", index: 1 },
        { title: "", index: 2 },
      ],
    };
  },
  created() {
    this.initialize();
  },
  methods: {
    home() {
      this.$router.push({ name: "Home" });
    },
    async initialize() {
      await Product.getUserProduct({
        limit: this.itemsPerPage,
        page: this.page,
        status: this.status,
        condition: this.condition,
        id: this.userId.id,
      })
        .then((res) => {
          this.desserts = res.data.products;
          this.count = res.data.Count;
           
        })
        .catch((err) => {
          console.log(err);
        });
    },
    deleteItem(item) {
      this.dialogDelete = true;
      this.editedIndex = item;
    },
    updateProduct(item) {
      this.dialogUpdate = true;
      this.editedIndex = item;
    },
    closeDialog(item) {
      this.dialogUpdate = false;
    },
    update() {},
    async auitYes() {
      await Product.updateProduct({
        id: this.editedIndex._id,
        data: { condition: 4, status: 4, delete: true },
      })
        .then((res) => {
          this.dialogDelete = false;
          this.initialize();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //
    getItem(data) {
      this.status = 1;
      if (data === "left") {
        this.condition = 0;
      } else if (data === "center") {
        this.condition = 1;
      } else if (data === "right") {
        this.condition = 2;
      } else if (data === "justify") {
        this.show = false;
        this.condition = 3;
      } else {
        console.log(123);
        this.status = null;
      }
      this.desserts = [];
      this.initialize();
    },
    // 

    getStatus(data) {
      this.condition = null;
      if (data == 0) {
        this.status = 0;
      } else if (data == 1) {
        this.status = 1;
      } else {
        this.status = 2;
      }
      this.desserts = [];
      this.initialize();
    },
  },
};
</script>

<style>
#bottom {
  position: fixed;
  right: 0px;
  bottom: 0px;
}
</style>